export default {
    data() {
        return {
            show:false,isCode:true
        }
    },
    components: {
        
    },
    beforeCreate(){
        this.$store.dispatch('user')
    },
    computed:{
        user(){
            return this.$store.state.user
        },
    },
    created(){
        document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
        document.title = '会员中心'
        this.$store.commit('ACTIVE', 3)
    },
    methods:{
        sign(){
            this.$store.dispatch('addIntegration', 2)
        },
        vip(){
            if(this.user.wtCustomer.phonenumber != ''){
                this.$router.push({path:'/Card'})
            }else{
                this.$dialog.confirm({
                    message: '您还没有绑定会员卡！',
                    confirmButtonText:'去绑定'
                }).then(() => {
                    this.$router.push({path:'/Opencard'})
                }).catch(()=>{

                });
            }
        },
        qrcodes() {
            this.show = !this.show
            if(this.isCode){
                this.$nextTick(()=>{
                    var qrcode = new QRCode(document.getElementById("qrcode"), {
                        width: 200, height: 200
                    })
                    function makeCode(e) { qrcode.makeCode(e) }
                    makeCode(this.user.wtCustomer.cardno)
                })
                this.isCode = false
            }
        },
    },
}



